import React from 'react';
import styles from '../styles/Header.module.css';
import { Navbar, Nav, Container, Form, FormControl } from 'react-bootstrap';
import { FaBell, FaCog, FaUserCircle } from 'react-icons/fa';
import { FaBarsStaggered } from "react-icons/fa6";

const Header = ({ isSidebarCollapsed, toggleSidebar }) => {
  return (
    <Navbar
      expand="lg"
      className={`${styles.navbar} ${isSidebarCollapsed ? styles.navbarCollapsed : ''}`}
    >
      <Container fluid className={styles.container}>
        {/* Toggle Button */}
        <span className={styles.toggleButton} onClick={toggleSidebar}>
          <FaBarsStaggered
            className={`${styles.toggleIcon} ${isSidebarCollapsed ? styles.toggleIconCollapsed : ''}`}
          />
        </span>

        {/* Search Bar */}
        <Form className={styles.searchForm}>
          <FormControl
            type="text"
            placeholder="Search..."
            className={styles.searchInput}
          />
        </Form>

        {/* Icons */}
        <Nav className="ms-auto">
          <Nav.Link href="#" className={styles.navIcon}>
            <FaBell />
          </Nav.Link>
          <Nav.Link href="#" className={styles.navIcon}>
            <FaCog />
          </Nav.Link>
          <Nav.Link href="#" className={styles.navIcon}>
            <FaUserCircle />
          </Nav.Link>
        </Nav>
      </Container>
    </Navbar>
  );
};

export default Header;
